<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">支出</span>
				</div>
			</div>
			<!--新增模块开始-->
			<div class="cash_flow_table table" v-if="is_add_page">
				<el-table ref="multipleTable" :data="addlist" border style="width: 100%" empty-text="">
					
					<el-table-column prop="coin" label="收入主体类别" width="200">
						<template slot-scope="scope">
							<el-select v-model="scope.row.fund_type" @change="chooseFundType" placeholder="请选择" clearable>
	    							<el-option v-for="item in fund_type_list" :key="item.value" :label="item.label" :value="item.value">
	    							</el-option>
							</el-select>
						</template>
					</el-table-column>
					<el-table-column prop="coin" label="收入主体名称" width="200">
						<template slot-scope="scope">
							<el-select v-model="scope.row.fund_id" @change="chooseFundName" placeholder="请选择" clearable filterable>
								<el-option v-for="item in fund_list" :key="item.id" :label="item.name" :value="item.fund_id">
								</el-option>
							</el-select>
						</template>
					</el-table-column>
					<el-table-column prop="date" label="日期" width="190">
						<template slot-scope="scope">
							<el-date-picker v-model="scope.row.date" @change="chooseDate($event)" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
							</el-date-picker>
						</template>
					</el-table-column>
					<el-table-column prop="event" label="事件" width="250">
						
					</el-table-column>
					<el-table-column prop="coin" label="币种" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.unit == 1">
								人民币
							</div>
							<div class="" v-if="scope.row.unit == 2">
								美元
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="output" label="金额（万元）" width="150">
						<template slot-scope="scope">
							<el-input v-model.number="scope.row.output" placeholder="请输入内容" clearable>
							</el-input>
						</template>
					</el-table-column>
					<el-table-column prop="note" label="备注" width="200">
						<template slot-scope="scope">
							<el-input v-model="scope.row.note" placeholder="请输入内容" clearable>
							</el-input>
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<div class="">
								<el-button @click="addSave(scope.row)" type="text" size="small">保存</el-button>
								<el-button @click="addCancel(scope.row)" type="text" size="small">取消</el-button>
							</div>
						</template>
					</el-table-column>
				</el-table>
			</div>
			
			<!--编辑查看模块-->
			<div class="cash_flow_table table" v-else>
				<el-table ref="multipleTable" :data="datalist" border style="width: 100%" empty-text="">
					<el-table-column prop="coin" label="收入主体类别" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.$index != currentIndex">
								{{ scope.row.fund_type_name }}
							</div>
							<div class="" v-else>
								<el-select v-model="scope.row.fund_type" @change="chooseFundType" placeholder="请选择" clearable>
		    							<el-option v-for="item in fund_type_list" :key="item.value" :label="item.label" :value="item.value">
		    							</el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="coin" label="收入主体名称" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.$index != currentIndex">
								{{ scope.row.fund_name }}
							</div>
							<div class="" v-else>
								<el-select v-model="scope.row.fund_id" @change="chooseFundName($event,scope.row)" placeholder="请选择" clearable filterable>
		    							<el-option v-for="item in fund_list" :key="item.fund_id" :label="item.name" :value="item.fund_id">
		    							</el-option>
								</el-select>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="date" label="日期" width="190">
						<template slot-scope="scope">
							<div class="" v-if="scope.$index != currentIndex">
								{{ scope.row.date }}
							</div>
							<div class="" v-else>
								<el-date-picker v-model="scope.row.date" @change="chooseDate($event)" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
								</el-date-picker>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="event" label="事件" width="250">
						
					</el-table-column>
					<el-table-column prop="unit" label="币种" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.unit == 1">
								人民币
							</div>
							<div class="" v-if="scope.row.unit == 2">
								美元
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="output" label="金额（万元）" width="150">
						<template slot-scope="scope">
							<div class="" v-if="scope.$index != currentIndex">
								{{ scope.row.output }}
							</div>
							<div class="" v-else>
								<el-input v-model.number="scope.row.output" placeholder="请输入内容" clearable>
								</el-input>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="note" label="备注" width="200">
						<template slot-scope="scope">
							<div class="" v-if="scope.$index != currentIndex">
								{{ scope.row.note }}
							</div>
							<div class="" v-else>
								<el-input v-model="scope.row.note" placeholder="请输入内容" clearable>
								</el-input>
							</div>
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150" v-if="is_editable">
						<template slot-scope="scope">
							<div class="">
								<el-button v-if="scope.$index != currentIndex" @click="edit(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
								<el-button v-if="scope.$index == currentIndex" @click="editSave(scope.row)" type="text" size="small">保存</el-button>
								<el-button v-if="scope.$index == currentIndex" @click="editCancel(scope.row)" type="text" size="small">取消</el-button>
							</div>
						</template>
					</el-table-column>
				</el-table>
			</div>
			
		</div>
	</div>
</template>

<script>
	import { addSaveCompanyRunCashPayApi,getSaveCompanyRunCashPayApi,editSaveCompanyRunCashPayApi,getSelectListCompanyRunCashPayApi,getEventByDateApi } from '../../config/api'
	import { dialogConfirm,dialogConfirmHasbtn,getSelectedItem,event_type_company,cash_type_pay } from '../../config/mUtils'
	
	export default {
		inject: ['reload'],
		
		data() {
			return {
				id: '',
				is_add_page: '',//是否是添加页面
				is_editable: 1,//汇总页面传过来的是否可编辑字段
				count: '',//查看页面传过来的参数
				n: '',////代表事件中的第几次支出
				currentIndex: -1,
				
				//选择收入主体时选中的基金的fund_id
				selected_fund_id: '',
				addlist: [
					{
						fund_type: '',
						fund_id: '',
						unit: '',
						event: '',
						date: '',
						output: '',
						note: '',
						fund_name: '',
						event_count: '',
					}
				],
				datalist: [
					{
						fund_type: '',
						fund_id: '',
						unit: '',
						event: '',
						date: '',
						output: '',
						note: '',
						selected_fund_name: '',//编辑时选择收入主体的名字
						event_count: '',
					}
				],
				fund_type_list: [
					{
						value: 1,
						label: '母基金'
					},
					{
						value: 2,
						label: '自管基金'
					},
				],
				fund_list: [],
				all_fund_list: [],
				coinList: [
					{
						label: '人民币',
						value: 1
					},
					{
						label: '美元',
						value: 2
					}
				]
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.is_add_page = this.$route.query.add;
			this.is_editable = this.$route.query.is_editable;
			this.getSelectListData()
			if (this.$route.query.count) {
				this.count = this.$route.query.count;
				this.getInitData()
			}
		},
		computed: {
	
		},
		mounted() {},
		methods: {
			getInitData(){
				getSaveCompanyRunCashPayApi({
					sg_company_manager_id: this.id,
					count: this.count
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.$set(this.datalist,0,res.data.payload.data);
						this.datalist[0].selected_fund_name = res.data.payload.data.fund_name;
						this.n = res.data.payload.data.event_count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getSelectListData(){
				getSelectListCompanyRunCashPayApi({
					sg_company_manager_id: this.id,
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.all_fund_list = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			chooseFundType(e){
				this.datalist[0].fund_id = '';
				this.addlist[0].fund_id = '';
				this.datalist[0].unit = '';
				this.addlist[0].unit = '';
				this.fund_list = this.all_fund_list[e];
			},
			chooseFundName(e,row){
				if (e) {
					var item = getSelectedItem(this.fund_list,'fund_id',e);
					this.selected_fund_id = item.fund_id;
					if (this.is_add_page) {
						this.addlist[0].fund_name = item.name;
						this.addlist[0].unit = item.unit;
					} else {
						this.datalist[0].selected_fund_name = item.name;
						this.datalist[0].unit = item.unit;
					}
					this.getEventCountData()
				} else {
					this.addlist[0].event = '';
					this.addlist[0].unit = '';
					this.datalist[0].event = '';
					this.datalist[0].unit = '';
				}
			},
			chooseDate(date){
				if (!date) {
					return
				}
				this.getEventCountData()
			},
			getEventCountData(){
				if (this.is_add_page) {
					if (this.addlist[0].fund_type == 1) {
						var fund_type = 2;
					} else{
						var fund_type = 3;
					}
					var date = this.addlist[0].date;
					var event_id = '';
				} else {
					if (this.datalist[0].fund_type == 1) {
						var fund_type = 2;
					} else{
						var fund_type = 3;
					}
					var date = this.datalist[0].date;
					var event_id = this.datalist[0].id;
				}
				getEventByDateApi({
					type: event_type_company,
					id: this.id,
					cash_type: cash_type_pay,
					date: date,
					fund_type: fund_type,
					fund_id: this.selected_fund_id,
					event_id: event_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						if (this.is_add_page) {
							this.addlist[0].event = res.data.payload.event_name;
							this.addlist[0].event_count = res.data.payload.event_count;
						} else{
							this.datalist[0].event = res.data.payload.event_name;
							this.datalist[0]['event_count'] = res.data.payload.event_count;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			edit(index,e){
				this.currentIndex = index;
				var param = String(e.fund_type);
				this.fund_list = this.all_fund_list[param];
			},
			editSave(e){
				if(e.output===''|| !e.date || !e.unit || !e.fund_type || !e.fund_id){
					dialogConfirm("info", '不能存在空值');
					return
				}
				if(!Number(e.output)){
					if(e.output!=0){
						dialogConfirm("info", '金额格式错误!');
						return false;
					}
				}
				editSaveCompanyRunCashPayApi({
					sg_company_manager_id: this.id,
					event: e.event,
					output: e.output,
					date: e.date,
					unit: e.unit,
					note: e.note,
					id: e.id,
					fund_type: e.fund_type,
					fund_id: e.fund_id,
					event_count: e.event_count
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.reload()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editCancel(e){
				this.reload()
			},
			addSave(e){
				if(e.output!==''&&e.date&&e.unit&&e.fund_type&&e.fund_id){
					if(!Number(e.output)){
						if(e.output!=0){
							dialogConfirm("info", '金额格式错误!');
							return false;
						}
					}
					addSaveCompanyRunCashPayApi({
						sg_company_manager_id: this.id,
						event: e.event,
						output: e.output,
						date: e.date,
						unit: e.unit,
						note: e.note,
						fund_type: e.fund_type,
						fund_id: e.fund_id,
						event_count: e.event_count
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.$router.go(-1);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}else{
					dialogConfirm("info", '不能存在空值!');
				}
				
			},
			addCancel(){
				this.$router.push({
					name: "cashFlowHome",
					query: {
						id: this.id
					}
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	@import '../../style/cashFlow.scss'; 
</style>


